@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
:root {
    --navy: #073642;
    --lightBlue: #586e75;
    --lightGray: #586e7576;
    --blue: #268bd2;
    --cyan: #2aa198;
    --lightCyan: #2aa1977c;
    --violet: #6c71c4;
    --red: #dc322f;
    --magenta: #d33682;
    --orange: #cb4b16;
    --green: #859900;
    --wheat: #fdf6e3;
    --light: #ffffff9e;
    --lightO: #fefcf4;
}

html {
    font-family: 'Ubuntu', sans-serif;
    font-size: 14px;
    background-color: #fdf6e3;
    background-color: var(--wheat);
    color: #073642;
    color: var(--navy);
}

body {
    margin-bottom: 510px;
}
footer {
    font-size: 10px;
    padding: 5px;
    color: #586e75;
    color: var(--lightBlue);
    margin: 10px;
}

.invisible {
    display: none;
}
.container {
    margin: auto;
    width: 90vw;
    margin-top: 2%;
    height: 90%;
}

.main-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

/* TABLE STYLING */

table {
    border-spacing: 0px;
}

tbody td {
    max-height: 50px;
}

.table-header th {
    color: #586e75;
    color: rgba(255, 255, 255, 0.92);
    border: 1px solid #586e7576;
    border: 1px solid var(--lightGray);
    background-color: rgb(216, 127, 97);
    padding: 10px;
}

.table-container td {
    border: 1px solid #586e7576;
    border: 1px solid var(--lightGray);
    max-height: 100px;

    padding: 5px 15px;
    line-height: 20px;
    overflow: scroll;
}

.table_row {
    background-color: #ffffff9e;
    background-color: var(--light);
    height: 50px;
}
.table_section {
    background-color: darksalmon;
    letter-spacing: 2px;
}

.table_section td {
    border: none;
    border-spacing: 0px;
    max-height: 50px !important;
}

th {
    color: #268bd2;
    color: var(--blue);
}

/* Table Cells */
.msg_name {
    width: 20px;
    text-align: center;
}

.port {
    width: 20px;
}

.sysex-cell {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    width: 200px;
}

.long {
    width: 35%;
}

.description {
    width: 5%;
}

.behavior {
    width: 15%;
    max-height: 100px;
}

input[type='textarea'] {
    /* height: 200px; */
    outline: none;
    height: 2rem;

    overflow: auto;

    text-align: start;
    background-color: #ffffff9e;
    background-color: var(--light);
    border: none;
}

/* End of Table Style */

.send-button {
    margin-top: 20px;
    width: 60%;
}

.send-button:hover {
    content: 'send';
}

.collection-container {
    border: 1px solid #073642;
    border: 1px solid var(--navy);
    padding: 10px;
    margin: 25px;
    width: 200px;

    position: fixed;
}

.pass {
    color: #859900;
    color: var(--green);
}

.fail {
    color: #cb4b16;
    color: var(--orange);
}

/* custom messages */
.messages-container {
    background-color: white;
    border: 1px solid #073642;
    border: 1px solid var(--navy);
    padding: 10px;
    overflow: auto;
}
.custom-message {
    padding: 10px 0px;
}
.messages {
    padding: 6px;
    cursor: pointer;
}
.messages:hover {
    background-color: rgba(176, 215, 222, 0.543);
    color: rgb(111, 165, 95);
    /* transition: all 0.2s; */
    border-radius: 5px;
}

.window {
    display: block;
    background-color: #ffffff9e;
    background-color: var(--light);
    border: 1px solid #586e75;
    border: 1px solid var(--lightBlue);
    border-radius: 5px;
    margin: auto;
    display: block;

    padding: 10px 30% 10px 5%;
}

.window__header {
    margin: auto;
}

.window__content {
    text-align: left;
}

.window__footer {
    font: 200;
}

.overflow {
    max-height: 100px;
    overflow: scroll;
    z-index: 0;
}

.copy__container {
    margin: auto;
    float: right;
    z-index: 99;
}

/* Send BUTTON */

.button {
    cursor: pointer;
    margin: 5px;
    border: 1px solid #586e75;
    border: 1px solid var(--lightBlue);
    background-color: #ffffff9e;
    background-color: var(--light);
    border-radius: 5px;
    padding: 5px 8px;
}

.button:hover {
    color: #2aa198;
    color: var(--cyan);
    transition: all 200ms;
}
.send-text {
    pointer-events: none;
}
.sent {
    border: 1px solid #2aa198 !important;
    border: 1px solid var(--cyan) !important;
    color: #fdf6e3;
    color: var(--wheat);
    background-color: #2aa198 !important;
    background-color: var(--cyan) !important;
    font-weight: bold;
}

.sent:hover {
    color: #fdf6e3 !important;
    color: var(--wheat) !important;
}

button:active {
    font-weight: bold;
    border: 1px solid #2aa198;
    border: 1px solid var(--cyan);
}
.sent:hover span {
    display: none;
}

.sent:hover:before {
    content: 'send';
}

.copy {
    width: 57px;
}

.copied {
    background-color: #2aa198;
    background-color: var(--cyan);

    color: #fdf6e3;

    color: var(--wheat);
}

.copied:hover {
    color: #fdf6e3;
    color: var(--wheat);
}

/* SHEET Select */

.sheet-select-overlay {
    height: 100vh;
    width: 100vw;
    z-index: 999;
    position: absolute;
    top: 0px;
    margin: auto;
    background-color: rgba(220, 220, 220, 0.687);
    border: 1px solid #586e75;
    border: 1px solid var(--lightBlue);
}

.sheet-select-overlay h2 {
    text-align: center;
    padding: 1rem;
    border: 2px solid #586e75;
    border: 2px solid var(--lightBlue);
    border-radius: 5px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom: none;
    color: #2aa198;
    color: var(--cyan);
    width: 50%;
    background-color: #fefcf4;
    background-color: var(--lightO);
    margin: auto;
    margin-top: 10%;
}
.sheet-container {
    margin: auto;
    width: 50%;

    min-height: 350px;
    padding: 1rem;
    border: 2px solid #586e75;
    border: 2px solid var(--lightBlue);
    border-radius: 5px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    color: #586e75;
    color: var(--lightBlue);
    background-color: #fefcf4;
    background-color: var(--lightO);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    -webkit-justify-content: space-around;

            justify-content: space-around;
}

.sheet {
    margin: 10px;
    min-width: 100px;
    min-height: 50px;
    -webkit-flex: 1 1 100px;
            flex: 1 1 100px;
    padding: 10px;
    cursor: pointer;
    border: 1px solid;
    border-radius: 5px;
    text-align: center;
    font-size: 1.2rem;
    color: #586e75;
    color: var(--lightBlue);
    background-color: #ffffff9e;
    background-color: var(--light);
    transition: all 500ms;
}
.sheet:hover {
    background-color: #2aa198;
    background-color: var(--cyan);
    color: white;
}

.title {
    font-size: 16px;
    text-align: center;
    letter-spacing: 2px;
    margin: auto;
}

.utilities {
    display: -webkit-flex;
    display: flex;
    margin: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--light);
    border: 1px solid var(--lightBlue);

    border-radius: 5px;
    padding: 10px 20px;
}

select {
    cursor: pointer;
    margin: 0px 5px;
}

input[type='file'] {
    display: none;
}

.midi-ports {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: center;
            align-content: center;
    padding: 5px;
    border: 1px solid var(--lightBlue);
    background-color: var(--light);
    border-radius: 5px;
    padding: 5px 18px;
}

/* let msg = items[target].sysex;
        let byteArray = msg.split(' ');
        */

.monitor-container {
    z-index: 10;
    height: 35%;
    width: 90%;
    min-width: 810px;
    min-height: 250px;
    border: 1px solid var(--lightBlue);
    border-radius: 5px;
    /* border-bottom: none; */
    /* border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px; */

    background-color: var(--lightO);
    position: fixed;
    padding-bottom: 1px;
    bottom: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: auto;
}

.monitor-title {
    justify-items: left;
    margin: 10px auto 10px 3%;
    border-radius: 5px;
}

.port-select-container {
    width: 30%;
    margin: auto;
}

.monitor-content {
    width: 95%;
    /* background-color: var(--light); */
    border-radius: 5px;

    margin: auto;

    height: 90%;
    min-height: 300px;
    display: -webkit-flex;
    display: flex;
    /* max-width: 90%; */
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.columns-container {
    height: 95%;
    margin: 0 10px;
    border-bottom: 1px solid var(--lightBlue);
    border-radius: 5px;
    min-width: 205px;
    border-top: none;
    border-spacing: 2rem;
    position: relative;
    /* width: auto; */
    /* max-width: 420px; */
    min-height: 220px;
    max-height: 99%;
    overflow: auto;
    resize: both;
}

.column-title {
    position: -webkit-sticky;
    position: sticky;
    padding: 5px;
    text-align: center;
    /* margin: 0 7px; */
    z-index: 999;

    border: 1px solid;
    border-radius: 5px;
    word-wrap: break-word;

    /* cursor: pointer; */
    background-color: var(--light);
    border-bottom-left-radius: 0px;

    border-bottom-right-radius: 0px;
}

.monitor-column {
    background-color: var(--light);
    border: 1px solid var(--lightBlue);
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-top: none;
    border-bottom: none;

    /* margin: 0 7px; */
    height: 90%;

    /* width: 100%; */
    /* width: 100%; */
    padding: 2px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: scroll;
    -webkit-align-content: center;
            align-content: center;
}

.midi-message {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;
}

.midi-message div {
    padding: 2px 10px;
}

#midi-input-note {
}
#midi-input-time {
    text-align: right;
}

/* for colors */
.orange {
    color: var(--orange);
}

.green {
    color: var(--green);
}
.violet {
    color: var(--violet);
}
.cyan {
    color: var(--cyan);
}

.magenta {
    color: var(--magenta);
}

